import React from 'react';

import utils from 'src/utils';

import IpShowcase from './ipShowcase';

const lang = window.localStorage.getItem('leading_lang');

const subTitles = ['Fashion', 'Accessories', 'Catering', 'Household', 'Toys'];
const links = ['./fashion', './accessories', './catering', './household', './toys'];

const items = utils.newArray(5).map(i => {
  return {
    title: utils.localize(`showcase.title${i + 1}`),
    subTitle: lang === 'zh' ? subTitles[i] : undefined,
    classes: `ip-showcase-${i + 1}`,
    style: i % 2 === 0 ? 'style1' : 'style2',
    link: links[i]
  };
});

class Showcase extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    document.title = `${utils.localize('menuItem3')} - ${utils.localize('companyName')}`;
  }

  render() {
    return (
      <div className="showcase">
        {
          items.map((item, i) => {
            return <IpShowcase key={i} {...item} />
          })
        }
      </div>
    );
  }
}

export default Showcase;